body {
  --sticky-height: 3em;
  --color1: #20232a;
  --color2: #61dafb;
  --color3: #f7f7f7;
  --color4: #6d6d6d;
  --color5: #ececec;
  --phi-a: calc(61.8vw - var(--scrollbar-width)  + 1px);
  --phi-b: calc(var(--sticky-height) * .5 + 38.2vw);
  font-family: Open Sans, sans-serif;
  overflow-x: hidden;
}

#main-navigation {
  width: var(--phi-b);
  height: var(--sticky-height);
  z-index: 1;
  grid-template-columns: 1fr 38.2vw;
  display: grid;
  position: fixed;
  top: 0;
  right: 0;
}

#main-navigation > :first-child {
  height: 50%;
  overflow: hidden;
}

#main-navigation > :first-child > :first-child {
  background: radial-gradient(circle at center left, transparent calc(.5 * var(--sticky-height)), var(--color1) 0) bottom left;
  height: 200%;
}

#main-navigation > :last-child {
  background-color: var(--color1);
  border-bottom-left-radius: calc(.25 * var(--sticky-height));
  -webkit-column-gap: 1em;
  -moz-column-gap: 1em;
  box-sizing: border-box;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 1em;
  padding: 0 1em 4px;
  transition: border-radius .25s ease-out;
  display: grid;
}

#main-navigation.attached > :last-child {
  border-radius: 0;
}

.navbutton {
  text-align: center;
  color: #fff;
  box-sizing: border-box;
  border: 3px inset var(--color2);
  user-select: none;
  border-top: 0;
  border-bottom-right-radius: 50%;
  border-bottom-left-radius: 50%;
  font-weight: bold;
  text-decoration: none;
  transition: color .382s;
}

.navbutton:hover {
  border: 4px inset var(--color2);
  color: var(--color2);
  border-top: 0;
}

#welcome {
  background-color: var(--color1);
  height: 100vh;
  position: relative;
}

#background-details {
  --size1: 38.2vh;
}

#background-details > .det-white-ball1 {
  top: calc(100vh - var(--size1));
  left: calc(var(--phi-a)  - var(--size1) * .5 - calc(.5 * var(--sticky-height)));
  width: var(--size1);
  height: var(--size1);
  background-color: var(--color3);
  border-radius: 100%;
  position: absolute;
}

#background-details > .det-white-rect1 {
  top: calc(100vh - var(--size1));
  background-color: var(--color3);
  width: calc(var(--phi-b));
  height: var(--size1);
  position: absolute;
  right: 0;
}

#background-details > .det-black-ball1 {
  top: calc(100vh - var(--size1) * 2);
  width: var(--size1);
  height: var(--size1);
  background-color: var(--color1);
  border-radius: 100%;
  position: absolute;
  right: 0;
}

#background-details > .det-white-rect2 {
  top: calc(100vh - var(--size1) * 1.5);
  width: calc(.5 * var(--size1));
  height: calc(.5 * var(--size1));
  background-color: var(--color3);
  position: absolute;
  right: 0;
}

#few-words, .filter-instruction {
  user-select: none;
}

.filter-instruction {
  text-align: center;
  margin-bottom: 0;
}

#few-words > * {
  --f-size: 50px;
  --l-height: 1.15;
  font-size: var(--f-size);
  position: absolute;
}

#few-words .sentence1 {
  color: #fff;
  margin: 0 12vw;
  animation: 1s ease-out 1s both text-appear;
  top: 20vh;
}

#few-words .sentence2 {
  color: #fff;
  top: calc(20vh + 1.2 * var(--f-size));
  margin: 0 12vw;
  animation: 1s ease-out 1.66s both text-appear;
}

#few-words .sentence3 {
  color: #fff;
  top: calc(20vh + 2.4 * var(--f-size));
  margin: 0 12vw;
  animation: 1s ease-out 2.32s both text-appear;
}

#few-words .sentence4 {
  color: #fff;
  background-color: var(--color1);
  bottom: calc(var(--f-size));
  text-align: center;
  width: 100%;
  line-height: var(--l-height);
}

#few-words .sentence4 > * {
  animation: 1s ease-out 3.32s both text-appear;
  display: inline-block;
  position: relative;
  bottom: 6px;
}

main {
  background-color: var(--color3);
  color: var(--color4);
  display: grid;
}

.items {
  --gap: 15px;
  padding: var(--gap);
  flex-wrap: wrap;
  justify-content: center;
  justify-self: center;
  display: flex;
}

.item {
  --border: 3px;
  --width: 50vw;
  box-sizing: border-box;
  width: var(--width);
  max-width: calc(331px + 2 * var(--border));
  margin: var(--gap);
  background-color: var(--color5);
}

.item > .snapshot {
  border: var(--border) solid black;
  max-height: 207px;
  text-decoration: none;
  display: grid;
  overflow: hidden;
}

.item > .snapshot > * {
  grid-area: 1 / 1 / 2 / 2;
}

.item > .snapshot > img {
  max-width: 100%;
  max-height: 100%;
  display: block;
}

.item > .snapshot > ul {
  opacity: 0;
  background: linear-gradient(#000 -20%, #0008 25% 75%, #000 120%);
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  transition: all .16s ease-in-out;
  display: flex;
}

.item > .snapshot:hover > ul {
  opacity: 1;
}

.item > .snapshot > ul > li {
  color: #fff;
  background-color: var(--color1);
  border: 1px outset var(--color4);
  opacity: 0;
  border-radius: 2px;
  padding: 5px;
  font-weight: bold;
  transition: all .16s ease-in-out;
  display: inline-block;
  transform: perspective(51cm)translateZ(-250px);
}

.item > .snapshot:hover > ul > li {
  opacity: 1;
  transition-delay: .16s;
  transform: perspective(51cm)translateZ(0);
}

.item > .title {
  text-align: center;
}

.item > .description, .item > .tags {
  padding: 0 5px;
}

#filter {
  z-index: 1;
  width: var(--phi-a);
  height: calc(var(--sticky-height)  + 1px);
  background-color: var(--color1);
  border-top-right-radius: calc(.5 * var(--sticky-height));
  grid-auto-flow: column;
  display: grid;
  position: sticky;
  top: -1px;
  overflow: auto;
}

.mobile #filter {
  border-radius: 0;
  width: 100vw;
}

.filter-button {
  cursor: pointer;
  color: #fff5;
  text-align: center;
  user-select: none;
  border-bottom: 0px solid var(--color2);
  padding: 4px;
  transition: border .382s, color .382s;
}

.filter-button.pressed {
  color: var(--color2);
  border-bottom: 5px solid var(--color2);
}

.filter-button:hover {
  color: var(--color2);
}

.filter-button > * {
  transition: top .382s;
  position: relative;
  top: .3em;
}

.filter-button.pressed > * {
  top: .8em;
}

#goodbye {
  background-color: var(--color1);
  color: #fff;
  grid-template-areas: "more contact";
  display: grid;
}

.mobile #goodbye {
  grid-template-areas: "more"
                       "contact";
}

#goodbye .more {
  grid-area: more;
}

#goodbye .more figure {
  display: grid;
}

#goodbye .more figcaption {
  text-align: center;
}

#goodbye ul {
  justify-self: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

#goodbye li {
  margin: 10px;
}

#goodbye a {
  color: unset;
  text-decoration: none;
}

#goodbye li a {
  box-sizing: border-box;
  border: 2px solid var(--color3);
  border-radius: 50%;
  place-items: center;
  width: 70px;
  height: 70px;
  transition: all .5s ease-in-out;
  display: grid;
}

#goodbye li a:hover {
  color: var(--color2);
  border: 2px solid var(--color2);
  transform: rotate(1turn);
}

#goodbye .contact {
  grid-area: contact;
  place-items: center;
  display: grid;
}

.mobile #goodbye .contact {
  padding-bottom: 10px;
}

#goodbye .contact a {
  color: var(--color2);
}

@keyframes text-appear {
  0% {
    opacity: 0;
    transform: translate(0, 10px);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}
